.container__game {
    height: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container__game > h1 {
    text-align: center;
}

.container__game > .container__game--img {
    display: flex;
    flex-direction: column;
}

.container__game > .container__game--img > .nes-balloon {
    top: 70px;
    right: -100px;
    width: max-content;
    opacity: 0;
    transition: opacity .5s jump-end;
    z-index: 10;
}

.container__game > .container__game--img > .guess__animation {
    opacity: 1;
}

.container__game > div > .pkm__img {
    width: 300px;
    filter: grayscale(100%) brightness(0%);
    -webkit-filter: grayscale(100%) brightness(0);
    -webkit-user-drag: none;
}

.container__game > div {
    margin-top: 1rem;
}

.container__game > div > .guess__animation {
    filter: grayscale(0%) brightness(100%);
    -webkit-filter: grayscale(0%) brightness(100%);
    transition: filter 1s linear;
}



@media screen and (max-width:768px) {
    .main__game {
        height: auto;
    }

    .container__game {
        height: 750px;
    }
}

@media screen and (max-width:480px) {
    .container__game {
        height: 700px;
    }
}

@media screen and (max-width:420px) {
    .container__game > h1 {
        font-size: 1.5rem;
    }

    .container__game > img {
        width: 250px;
    }
}